<template>

  <div v-if="vm.currentTab=='LEVELS' && vm.currentTab2!=''">
    
    <h2 v-show="vm.currentScreen == 'PROPERTIES' || vm.currentScreen == 'INITIAL_OBJECTS' || vm.currentScreen == 'LEVEL_TEXTS' || vm.currentScreen == 'BUTTONS' || vm.currentScreen == 'LEVEL_VARIABLES' || vm.currentScreen == 'LEVEL_EVENTS'">
      <button type="button" class="btn btn-secondary back-button" v-on:click="vm.cancelChangesLevel();"><IconBack/> Back</button>
      {{vm.oldLevel.name}}
    </h2>

    <ul class="nav nav-tabs justify-content-center" v-show="vm.currentScreen == 'PROPERTIES' || vm.currentScreen == 'INITIAL_OBJECTS' || vm.currentScreen == 'LEVEL_TEXTS' || vm.currentScreen == 'BUTTONS' || vm.currentScreen == 'LEVEL_VARIABLES' || vm.currentScreen == 'LEVEL_EVENTS'">
      <li class="nav-item">
      <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab2('PROPERTIES');" id="link-tab-PROPERTIES">Properties</a>
      </li>
      <li class="nav-item">
      <a class="nav-link active" href="" v-on:click.prevent="vm.setCurrentTab2('INITIAL_OBJECTS');" id="link-tab-INITIAL_OBJECTS">Objects</a>
      </li>
      <li class="nav-item">
      <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab2('LEVEL_TEXTS');" id="link-tab-LEVEL_TEXTS">Texts</a>
      </li>
      <li class="nav-item">
      <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab2('BUTTONS');" id="link-tab-BUTTONS">Buttons</a>
      </li>
      <li class="nav-item">
      <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab2('LEVEL_VARIABLES');" id="link-tab-LEVEL_VARIABLES">Level variables</a>
      </li>
      <li class="nav-item">
      <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab2('LEVEL_EVENTS');" id="link-tab-LEVEL_EVENTS">Events</a>
      </li>
    </ul>

    <br>
    <div class="flex-container">
      <div v-show="vm.currentScreen == 'PROPERTIES'">
        <table>
          <tr>
            <td>name</td><td><input type="text" v-model="vm.newLevel.name"></td>
          </tr>
          <tr>
            <td>background type</td>
            <td>
              <select v-model="vm.newLevel.backgroundType">
                <option v-for="backgroundType in vm.backgroundTypes" v-bind:key="backgroundType.code" :value="backgroundType.code">{{backgroundType.name}}</option>
              </select>
            </td>
          </tr>
          <tr v-show="vm.newLevel.backgroundType=='IMAGE'">
            <td>background image</td>
            <td>
              <!--
              <select v-model="vm.newLevel.backgroundImageId">
                <option v-for="image in vm.images" v-bind:key="image.id" :value="image.id">{{image.id}}</option>
              </select>
              -->

              <!--<img :src="resourcesBaseUrl + vm.newLevel.backgroundImageId">-->
              <img v-if="vm.newLevel.backgroundImage.imageSourceType=='LIBRARY'" :src="resourcesBaseUrl + vm.newLevel.backgroundImage.imageId" class="image-large">
              <img v-if="vm.newLevel.backgroundImage.imageSourceType=='UPLOAD'" :src="vm.newLevel.backgroundImage.customImage.imageData" class="image-large">


              <br><br>
              <button type="button" class="btn btn-sm btn-primary" v-on:click="vm.openImageSelection('PROPERTIES');">Change image</button>
            </td>
          </tr>
          <tr v-show="vm.newLevel.backgroundType=='SOLID_COLOR'">
            <td>backgound color</td>
            <td>
              <div>red <input type="text" v-model="vm.newLevel.backgroundColor.red" style="width: 30px"></div>
              <div>green <input type="text" v-model="vm.newLevel.backgroundColor.green" style="width: 30px"></div>
              <div>blue <input type="text" v-model="vm.newLevel.backgroundColor.blue" style="width: 30px"></div>
            </td>
          </tr>
          <tr>
            <td>gravity</td><td><input type="text" v-model="vm.newLevel.gravity"></td>
          </tr>
          <tr>
            <td>electric constant</td><td><input type="text" v-model="vm.newLevel.electricConstant"></td>
          </tr>
          <tr>
            <td>center camera at object</td><td><input type="checkbox" v-model="vm.newLevel.centerCameraAtObject"></td>
          </tr>
          <tr v-show="vm.newLevel.centerCameraAtObject">
            <td>center camera at object type</td>
            <td>
              <select v-model="vm.newLevel.centerCameraAtObjectTypeId">
                <option v-for="customObjectType in vm.gameParamsJson.customObjectTypes" v-bind:key="customObjectType.id" :value="customObjectType.id">{{customObjectType.id}}</option>
              </select>
            </td>
          </tr>
          <tr>
            <td>apply gravitational force between objects</td><td><input type="checkbox" v-model="vm.newLevel.applyGravitationalForceBetweenObjects"></td>
          </tr>
          <tr v-show="vm.newLevel.applyGravitationalForceBetweenObjects">
            <td>gravitational constant</td><td><input type="text" v-model="vm.newLevel.gravitationalConstant"></td>
          </tr>
        </table>
        <br>
        <button type="button" class="btn btn-primary" v-on:click="vm.updateLevel();">save changes</button>
        <button type="button" class="btn btn-secondary" v-on:click="vm.cancelChangesLevel();">cancel changes</button>
      </div>
      <div v-show="vm.currentScreen == 'INITIAL_OBJECTS'">
        <table class="hiscores" v-show="vm.oldLevel.initialObjects.length > 0">
          <thead>
            <tr>
              <th></th>
              <th colspan="2">type</th>
              <th>edit</th>
              <th>delete</th>
            </tr>
          </thead>
          <tr v-for="(customObject, $index) in vm.oldLevel.initialObjects" v-bind:key="customObject.typeId">
            <td>{{$index+1}}</td>
            <td>
              <!--<img class="image-small" :src="resourcesBaseUrl + customObject.objectType.imageId">-->
              <img class="image-small" v-if="customObject.objectType.image.imageSourceType=='LIBRARY'" :src="resourcesBaseUrl + customObject.objectType.image.imageId">
              <img class="image-small" v-if="customObject.objectType.image.imageSourceType=='UPLOAD'" :src="customObject.objectType.image.customImage.imageData">
            </td>
            <td style="text-align: left">
              {{customObject.objectType.id}}
            </td>
            <td><a href="" v-on:click.prevent="vm.editCustomObject(customObject)">edit</a></td>
            <td><a href="" v-on:click.prevent="vm.deleteCustomObject(vm.currentLevelIndex,$index)">delete</a></td>
          </tr>
        </table>
        <br v-show="vm.oldLevel.initialObjects.length > 0">
        <button type="button" class="btn btn-primary" v-on:click="vm.addCustomObject(vm.currentLevelIndex)"><IconAdd/> Add object</button>
        <br><br>
      </div>
      <div v-show="vm.currentScreen == 'BUTTONS'">
        <table v-show="vm.oldLevel.buttons.length > 0">
          <thead>
            <tr>
              <th></th>
              <th v-show="false">uid</th>
              <th>name</th>
              <th>text</th>
              <th>edit</th>
              <th>delete</th>
            </tr>
          </thead>
          <tr v-for="(button, $index) in vm.oldLevel.buttons" v-bind:key="button.uid">
            <td>{{$index+1}}</td>
            <td v-show="false">{{button.uid}}</td>
            <td class="limitWidth limitWidth120">{{button.name}}</td>
            <td class="limitWidth limitWidth120">{{button.text}}</td>
            <td><a href="" v-on:click.prevent="vm.editButton(button)">edit</a></td>
            <td><a href="" v-on:click.prevent="vm.deleteButton(vm.currentLevelIndex,$index)">delete</a></td>
          </tr>
        </table>
        <br v-show="vm.oldLevel.buttons.length > 0">
        <button type="button" class="btn btn-primary" v-on:click="vm.addButton(vm.currentLevelIndex)"><IconAdd/> Add button</button>
        <br><br>
      </div>
      <div v-show="vm.currentScreen == 'LEVEL_VARIABLES'">
        <table class="hiscores" v-show="vm.oldLevel.levelVariables.length > 0">
          <thead>
            <tr>
              <th></th>
              <th>name</th>
              <th>initial value</th>
              <th>edit</th>
              <th>delete</th>
            </tr>
          </thead>
          <tr v-for="(levelVariable, $index) in vm.oldLevel.levelVariables" v-bind:key="levelVariable.name">
            <td>{{$index+1}}</td>
            <td>{{levelVariable.name}}</td>
            <td>{{levelVariable.initialValue}}</td>
            <td><a href="" v-on:click.prevent="vm.editLevelVariable(levelVariable)">edit</a></td>
            <td><a href="" v-on:click.prevent="vm.deleteLevelVariable(vm.currentLevelIndex,$index)">delete</a></td>
          </tr>
        </table>
        <br v-show="vm.oldLevel.levelVariables.length > 0">
        <button type="button" class="btn btn-primary" v-on:click="vm.addLevelVariable(vm.currentLevelIndex)"><IconAdd/> Add level variable</button>
        <br>
        <br>
      </div>
      <div v-show="vm.currentScreen == 'LEVEL_TEXTS'">
        <table class="hiscores" v-show="vm.oldLevel.texts.length > 0">
          <thead>
            <tr>
              <th></th>
              <th>text</th>
              <th>edit</th>
              <th>delete</th>
            </tr>
          </thead>
          <tr v-for="(levelText, $index) in vm.oldLevel.texts" v-bind:key="levelText.uid">
            <td>{{$index+1}}</td>
            <td class="limitWidth limitWidth120">{{levelText.text}}</td>
            <td><a href="" v-on:click.prevent="vm.editLevelText(levelText)">edit</a></td>
            <td><a href="" v-on:click.prevent="vm.deleteLevelText(vm.currentLevelIndex,$index)">delete</a></td>
          </tr>
        </table>
        <br v-show="vm.oldLevel.texts.length > 0">
        <button type="button" class="btn btn-primary" v-on:click="vm.addLevelText(vm.currentLevelIndex)"><IconAdd/> Add text</button>
        <br>
        <br>
      </div>
      <div v-show="vm.currentScreen == 'LEVEL_EVENTS'">
        
        <button type="button" class="btn btn-primary" v-on:click="vm.addActionBlock(vm.currentLevelIndex)"><IconAdd/> Add action</button>
        <button type="button" class="btn btn-primary" v-on:click="vm.addConditionBlock(vm.currentLevelIndex)"><IconAdd/> Add condition</button>
        <button type="button" class="btn btn-primary" v-on:click="vm.addLoopBlock(vm.currentLevelIndex)"><IconAdd/> Add loop</button>
        <br>
        <br>
      </div>
      <div v-show="vm.currentScreen == 'PROPERTIES' || vm.currentScreen == 'INITIAL_OBJECTS' || vm.currentScreen == 'LEVEL_TEXTS' || vm.currentScreen == 'BUTTONS' || vm.currentScreen == 'LEVEL_VARIABLES'" class="level-preview-div">
        <canvas v-on:mousemove="vm.levelPreviewViewModel.previewCanvasMouseMove($event)" v-on:mouseup="vm.levelPreviewViewModel.previewCanvasMouseUp($event)" v-on:mousedown="vm.levelPreviewViewModel.previewCanvasMouseDown($event)" id="previewCanvas" width="600" height="500" style="border:1px solid #000000;"></canvas>
      </div>
      <div v-show="vm.currentScreen == 'PROPERTIES' || vm.currentScreen == 'INITIAL_OBJECTS' || vm.currentScreen == 'LEVEL_TEXTS' || vm.currentScreen == 'BUTTONS' || vm.currentScreen == 'LEVEL_VARIABLES'">
        
        <div>
          <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#gridSettings" v-on:click="vm.levelPreviewViewModel.editGridConfig()"><IconCog/> Grid settings</button>
        </div>

        <div class="coordinates" v-if="vm.levelPreviewViewModel.levelPreviewRenderer.currentMousePos">
          x: {{vm.levelPreviewViewModel.previewCanvasMouseX}}
          y: {{vm.levelPreviewViewModel.previewCanvasMouseY}}
        </div>
        
        <div class="previewCanvasSelectedObject">
          <b>Selected element:</b>
          <div v-if="vm.levelPreviewViewModel.selectedText">
            <div>text: {{vm.levelPreviewViewModel.selectedText.text}}</div>
            <div>x: {{vm.levelPreviewViewModel.selectedText.x}}</div>
            <div>y: {{vm.levelPreviewViewModel.selectedText.y}}</div>
            <div>font family: {{vm.levelPreviewViewModel.selectedText.fontFamily}}</div>
            <div>font size: {{vm.levelPreviewViewModel.selectedText.fontSize}}</div>
            <div>align: {{vm.levelPreviewViewModel.selectedText.align}}</div>
            <button type="button" class="btn btn-primary" v-on:click="vm.levelPreviewViewModel.editSelectedElement()"><IconEdit/> edit</button>
            <button type="button" class="btn btn-primary" v-on:click="vm.levelPreviewViewModel.cloneSelectedObject()"><IconClone/> clone</button>
            <button type="button" class="btn btn-danger" v-on:click="vm.levelPreviewViewModel.deleteSelectedElement()"><IconDelete/> delete</button>
          </div>
          <div v-if="vm.levelPreviewViewModel.selectedObject">
            <!--<img class="image-small" :src="resourcesBaseUrl + vm.levelPreviewViewModel.selectedObject.objectType.imageId">-->
            <img class="image-small" v-if="vm.levelPreviewViewModel.selectedObject.objectType.image.imageSourceType=='LIBRARY'" :src="resourcesBaseUrl + vm.levelPreviewViewModel.selectedObject.objectType.image.imageId">
            <img class="image-small" v-if="vm.levelPreviewViewModel.selectedObject.objectType.image.imageSourceType=='UPLOAD'" :src="vm.levelPreviewViewModel.selectedObject.objectType.image.customImage.imageData">
            <div>type: {{vm.levelPreviewViewModel.selectedObject.objectType.id}}</div>
            <div>x: {{vm.levelPreviewViewModel.selectedObject.x}}</div>
            <div>y: {{vm.levelPreviewViewModel.selectedObject.y}}</div>
            <button type="button" class="btn btn-primary" v-on:click="vm.levelPreviewViewModel.editSelectedElement()"><IconEdit/> edit</button>
            <button type="button" class="btn btn-primary" v-on:click="vm.levelPreviewViewModel.cloneSelectedObject()"><IconClone/> clone</button>
            <button type="button" class="btn btn-danger" v-on:click="vm.levelPreviewViewModel.deleteSelectedElement()"><IconDelete/> delete</button>
          </div>
          <div v-if="vm.levelPreviewViewModel.selectedButton" class="divContainer">
            <div class="limitWidth limitWidth200">name: {{vm.levelPreviewViewModel.selectedButton.name}}</div>
            <div class="limitWidth limitWidth200">text: {{vm.levelPreviewViewModel.selectedButton.text}}</div>
            <div>x: {{vm.levelPreviewViewModel.selectedButton.x}}</div>
            <div>y: {{vm.levelPreviewViewModel.selectedButton.y}}</div>
            <div>width: {{vm.levelPreviewViewModel.selectedButton.width}}</div>
            <div>height: {{vm.levelPreviewViewModel.selectedButton.height}}</div>
            <div>font size: {{vm.levelPreviewViewModel.selectedButton.fontSize}}</div>
            <button type="button" class="btn btn-primary" v-on:click="vm.levelPreviewViewModel.editSelectedElement()"><IconEdit/> edit</button>
            <button type="button" class="btn btn-primary" v-on:click="vm.levelPreviewViewModel.cloneSelectedObject()"><IconClone/> clone</button>
            <button type="button" class="btn btn-danger" v-on:click="vm.levelPreviewViewModel.deleteSelectedElement()"><IconDelete/> delete</button>
          </div>
          <div v-if="!vm.levelPreviewViewModel.selectedObject && !vm.levelPreviewViewModel.selectedText && !vm.levelPreviewViewModel.selectedButton">
            Select an element in canvas to view details.
          </div>
        </div>

      </div>
    </div>



    <!-- Modal - grid settings -->
    <div class="modal fade" id="gridSettings" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Grid settings</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div v-if="vm.levelPreviewViewModel.newGridConfig" class="modal-body">
            <div class="custom-control custom-switch">
              <input v-model="vm.levelPreviewViewModel.newGridConfig.drawGrid" type="checkbox" class="custom-control-input" id="drawGrid">
              <label class="custom-control-label" for="drawGrid">Draw grid</label>
            </div>

            <div class="custom-control custom-switch">
              <input v-model="vm.levelPreviewViewModel.newGridConfig.snapToGrid" type="checkbox" class="custom-control-input" id="snapToGrid">
              <label class="custom-control-label" for="snapToGrid">Snap to grid</label>
            </div>

            <table class="hiscores">
              <tr>
                <td>Grid width</td><td><input type="text" v-model="vm.levelPreviewViewModel.newGridConfig.gridWidth"></td>
              </tr>
              <tr>
                <td>Grid height</td><td><input type="text" v-model="vm.levelPreviewViewModel.newGridConfig.gridHeight"></td>
              </tr>
              <tr>
                <td>Grid origin x</td><td><input type="text" v-model="vm.levelPreviewViewModel.newGridConfig.gridOriginX"></td>
              </tr>
              <tr>
                <td>Grid origin y</td><td><input type="text" v-model="vm.levelPreviewViewModel.newGridConfig.gridOriginY"></td>
              </tr>
            </table>


          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="vm.levelPreviewViewModel.updateGridConfig()">Update</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
          </div>
        </div>
      </div>
    </div>


  </div>
  
</template>

<script>
import IconBack from '@/components/icons/IconBack.vue';
import IconAdd from '@/components/icons/IconAdd.vue';
import IconEdit from '@/components/icons/IconEdit.vue';
import IconDelete from '@/components/icons/IconDelete.vue';
import IconClone from '@/components/icons/IconClone.vue';
import IconCog from '@/components/icons/IconCog.vue';

export default {
  name: 'LevelsTab',
  props: {
    vm: Object
  },
  components: {
    IconBack, IconAdd, IconEdit, IconDelete, IconClone, IconCog
  },
	data() {
		return {
			resourcesBaseUrl: process.env.VUE_APP_RESOURCES_BASE_URL
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.back-button {
  position: absolute;
  margin-left:-100px;
}

.image-small {
	max-width: 32px;
	max-height: 32px;
}

.previewCanvasSelectedObject {
  margin-top: 5px;
  background-color: white;
  border-radius: 5px;
  padding: 5px;
  border: 1px solid #cccccc;
}

div.coordinates {
  margin-top: 5px;
  color: #666666;
  background-color: #dddddd;
  padding: 3px;
  font-size: 0.8em;
  border-radius: 5px;
  border: 1px solid #cccccc;
}

.limitWidth {
  /*text-align: left;*/
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-left: auto;
  margin-right: auto;
}

.limitWidth120 {
  max-width: 120px;
}

.limitWidth200 {
  max-width: 240px;
}

.image-large {
	max-width: 128px;
	max-height: 128px;
}

</style>
