<template>

	<div v-if="vm.currentScreen=='CUSTOM_IMAGES'">
		<h2>Images</h2>
		<table class="hiscores">
			<thead>
				<tr>
					<th></th>
					<th>name</th>
					<th>image</th>
					<th>edit</th>
					<th>delete</th>
				</tr>
			</thead>
			<tr v-for="(customImage, $index) in vm.gameParamsJson.customImages" v-bind:key="customImage.name">
				<td>{{$index+1}}</td>
				<td>{{customImage.name}}</td>
				<td><img v-if="customImage.imageData" :src="customImage.imageData" style="max-width: 64px;"/></td>
				<td><a href="" v-on:click.prevent="vm.editCustomImage(customImage)">edit</a></td>
				<td><a href="" v-on:click.prevent="vm.deleteCustomImage($index)">delete</a></td>
			</tr>
		</table>
		<br>
		<button type="button" class="btn btn-primary" v-on:click="vm.goToAddCustomImage();"><IconAdd/> Upload image</button>
	</div>
  
</template>

<script>
import IconAdd from '@/components/icons/IconAdd.vue';

export default {
  name: 'CustomImages',
  components: {
    IconAdd
  },
  props: {
    vm: Object
  }
}
</script>

