<template>

  <div v-if="vm.currentScreen=='EDIT_LEVEL_TEXT'">
    <h2>Edit text</h2>
    <table class="hiscores">
      <tr>
        <td>text</td><td><input type="text" v-model="vm.newLevelText.text"></td>
      </tr>
      <tr>
        <td>x</td><td><input type="text" v-model="vm.newLevelText.x"></td>
      </tr>
      <tr>
        <td>y</td><td><input type="text" v-model="vm.newLevelText.y"></td>
      </tr>
      <tr>
				<td>fontFamily</td>
				<td>
					<!--<input type="text" v-model="vm.newLevelText.fontFamily">-->
					<select v-model="vm.newLevelText.fontFamily">
						<option v-for="fontFamilyOption in vm.fontFamilyOptions" v-bind:key="fontFamilyOption.code" :value="fontFamilyOption.code">{{fontFamilyOption.code}}</option>
					</select>
				</td>
      </tr>
      <tr>
        <td>fontSize</td><td><input type="text" v-model="vm.newLevelText.fontSize"></td>
      </tr>
      <tr>
				<td>align</td>
				<td>
					<select v-model="vm.newLevelText.align">
						<option v-for="textAlignOption in vm.textAlignOptions" v-bind:key="textAlignOption.code" :value="textAlignOption.code">{{textAlignOption.code}}</option>
					</select>
				</td>
      </tr>
      <tr>
				<td>color</td>
				<td>
          <table class="colorTable">
            <tr><td>red</td><td><input type="text" v-model="vm.newLevelText.color.red" style="width: 40px"></td></tr>
            <tr><td>green</td><td><input type="text" v-model="vm.newLevelText.color.green" style="width: 40px"></td></tr>
            <tr><td>blue</td><td><input type="text" v-model="vm.newLevelText.color.blue" style="width: 40px"></td></tr>
            <tr><td>alpha</td><td><input type="text" v-model="vm.newLevelText.color.alpha" style="width: 40px"></td></tr>
          </table>
				</td>
			</tr>
    </table>
    <br>
    <button type="button" class="btn btn-primary" v-on:click="vm.updateLevelText();">save changes</button>
    <button type="button" class="btn btn-secondary" v-on:click="vm.cancelChangesLevelText();">cancel changes</button>
    <br><br>
  </div>
  
</template>

<script>
export default {
  name: 'EditLevelText',
  props: {
    vm: Object
  }
}
</script>

