<template>
  
  <div>

    <Header v-show="showHeader && (vm.currentScreen == 'LEVELS' || vm.currentScreen == 'CUSTOM_OBJECT_TYPES' || vm.currentScreen == 'CUSTOM_TEXTS' || vm.currentScreen == 'GLOBAL_VARIABLES' || vm.currentScreen == 'CUSTOM_IMAGES' || vm.currentScreen == 'EVENTS' || vm.currentScreen == 'IMPORT_EXPORT' || vm.currentScreen == 'SAVE')"></Header>

    <div class="game-editor-div">

      <div v-if="errorMessage != null">
        <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
      </div>

      <div v-if="loading"><Spinner></Spinner></div>


      <div v-show="vm.isPlayingGame">
        <canvas id="myCanvas" width="600" height="500" style="border:1px solid #000000;">
        </canvas>
      </div>


      <p v-show="vm.currentScreen == 'LEVELS' || vm.currentScreen == 'CUSTOM_OBJECT_TYPES' || vm.currentScreen == 'CUSTOM_TEXTS' || vm.currentScreen == 'GLOBAL_VARIABLES' || vm.currentScreen == 'CUSTOM_IMAGES' || vm.currentScreen == 'EVENTS' || vm.currentScreen == 'IMPORT_EXPORT' || vm.currentScreen == 'SAVE'">
        <router-link to="/" v-show="!showHeader">
          <img class="logoGameEditor" alt="EasyGameMaker logo" src="../../../assets/easy-game-maker-logo.png">
        </router-link>
        <button type="button" class="btn btn-light" v-on:click="showHeader=true" v-if="!vm.isPlayingGame && !showHeader"><IconShowMore/> Show header</button>
        <button type="button" class="btn btn-light" v-on:click="showHeader=false" v-if="!vm.isPlayingGame && showHeader"><IconShowLess/> Hide header</button>
        <button type="button" class="btn btn-success" v-on:click="vm.initGame();" v-if="!vm.isPlayingGame"><IconPlay/> Play game</button>
        <button type="button" class="btn btn-primary" v-on:click="vm.initGame();" v-if="vm.isPlayingGame"><IconRestart/> Restart</button>
        <button type="button" class="btn btn-primary" v-on:click="vm.editGame()" v-if="vm.isPlayingGame"><IconEdit/> Edit game</button>
        <button type="button" class="btn btn-primary" v-on:click="vm.newGame()" v-if="!vm.isPlayingGame"><IconAdd/> New game</button>
        <button type="button" class="btn btn-primary" v-on:click="vm.requestCanvasFullscreen()" v-if="vm.isPlayingGame"><IconFullscreen/> Fullscreen</button>
        <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#exampleModal"><IconHelp/></button>
      </p>


      <div v-show="!vm.isPlayingGame">


        <ul class="nav nav-tabs justify-content-center" v-show="vm.currentScreen == 'LEVELS' || vm.currentScreen == 'CUSTOM_OBJECT_TYPES' || vm.currentScreen == 'CUSTOM_TEXTS' || vm.currentScreen == 'CUSTOM_IMAGES' || vm.currentScreen == 'GLOBAL_VARIABLES' || vm.currentScreen == 'EVENTS' || vm.currentScreen == 'IMPORT_EXPORT' || vm.currentScreen == 'SAVE'">
          <li class="nav-item">
          <a class="nav-link active" href="" v-on:click.prevent="vm.setCurrentTab('LEVELS');" id="link-tab-LEVELS">Levels</a>
          </li>
          <li class="nav-item">
          <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('CUSTOM_OBJECT_TYPES');" id="link-tab-CUSTOM_OBJECT_TYPES">Object Types</a>
          </li>

          <!--
          <li class="nav-item">
          <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('CUSTOM_TEXTS');" id="link-tab-CUSTOM_TEXTS">Texts</a>
          </li>
          -->

          <li class="nav-item">
          <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('CUSTOM_IMAGES');" id="link-tab-CUSTOM_IMAGES">Images</a>
          </li>

          <li class="nav-item">
          <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('GLOBAL_VARIABLES');" id="link-tab-GLOBAL_VARIABLES">Global variables</a>
          </li>
          <li class="nav-item">
          <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('EVENTS');" id="link-tab-EVENTS">Events</a>
          </li>
          
          <li class="nav-item">
          <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('IMPORT_EXPORT');" id="link-tab-IMPORT_EXPORT">Export</a>
          </li>
          
          <!--
          <li class="nav-item">
          <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('SAVE');" id="link-tab-SAVE">Save Online</a>
          </li>
          -->
        </ul>
        <br>

      

        <Levels :vm="vm"></Levels>
        <EditLevel :vm="vm"></EditLevel>
        <EditCustomObject :vm="vm"></EditCustomObject>
        <EditButton :vm="vm"></EditButton>
        <EditLevelVariable :vm="vm"></EditLevelVariable>
        <EditLevelText :vm="vm"></EditLevelText>
        <CustomObjectTypes :vm="vm"></CustomObjectTypes>
        <CustomObjectTypesTab :vm="vm"></CustomObjectTypesTab>
        <CustomObjectTypeBehaviour :vm="vm"></CustomObjectTypeBehaviour>
        <EditObjectVariable :vm="vm"></EditObjectVariable>
        <GlobalVariables :vm="vm"></GlobalVariables>
        <EditGlobalVariable :vm="vm"></EditGlobalVariable>
        <CustomImages :vm="vm"></CustomImages>
        <AddCustomImage :vm="vm"></AddCustomImage>
        <EditCustomImage :vm="vm"></EditCustomImage>
        <!--
        <CustomTexts :vm="vm"></CustomTexts>
        <EditCustomText :vm="vm"></EditCustomText>
        -->
        <Events :vm="vm"></Events>
        <EditEvent :vm="vm"></EditEvent>
        <EditLoop :vm="vm"></EditLoop>
        <EditEventCondition :vm="vm"></EditEventCondition>
        <EditEventAction :vm="vm"></EditEventAction>
        <ImageSelection :vm="vm"></ImageSelection>
        <ObjectTypeSelection :vm="vm"></ObjectTypeSelection>
        <ImportExport :vm="vm"></ImportExport>
        <SaveOnline :vm="vm"></SaveOnline>


      </div>



    <InsertExpression :vm="vm"></InsertExpression>


    <!--<div v-if="vm.environment" class="engineVersion">Engine version: {{vm.environment.templateVersion}}</div>-->



      <!-- Modal -->
      <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">Help</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <img class="logo" alt="EasyGameMaker logo" src="../../../assets/easy-game-maker-logo.png">
              <br>
              <p v-if="vm.environment">Engine version: {{vm.environment.templateVersion}}</p>
              
              <p>
                <a href="https://docs.easygamemaker.com/" target="_blank">Documentation</a>
              </p>

              <p>
                <a href="https://www.youtube.com/channel/UCZyU80zuPtOEYfNViTT1c1Q" target="_blank">Video tutorials</a>
              </p>

              <p>
                <a href="https://easygamemaker.itch.io/easy-game-maker/community" target="_blank">Discussion board</a>
              </p>

            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>



    </div>
  </div>
</template>


<script>
import axios from 'axios';
//import {getCookie} from '@/lib/mmgUtil.js';
import {loadEngine} from '@/lib/mmgUtil.js';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
import IconHelp from '@/components/icons/IconHelp.vue';
import IconPlay from '@/components/icons/IconPlay.vue';
import IconAdd from '@/components/icons/IconAdd.vue';
import IconRestart from '@/components/icons/IconRestart.vue';
import IconEdit from '@/components/icons/IconEdit.vue';
import IconFullscreen from '@/components/icons/IconFullscreen.vue';
import IconShowMore from '@/components/icons/IconShowMore.vue';
import IconShowLess from '@/components/icons/IconShowLess.vue';
import ImportExport from './components/ImportExport.vue';
import Levels from './components/Levels.vue';
import EditLevel from './components/EditLevel.vue';
import EditLevelVariable from './components/EditLevelVariable.vue';
import EditLevelText from './components/EditLevelText.vue';
import CustomObjectTypes from './components/CustomObjectTypes.vue';
import EditCustomObject from './components/EditCustomObject.vue';
import EditButton from './components/EditButton.vue';
import CustomObjectTypesTab from './components/CustomObjectTypesTab.vue';
import CustomObjectTypeBehaviour from './components/CustomObjectTypeBehaviour.vue';
import EditObjectVariable from './components/EditObjectVariable.vue';
import GlobalVariables from './components/GlobalVariables.vue';
import EditGlobalVariable from './components/EditGlobalVariable.vue';
import CustomImages from './components/CustomImages.vue';
import AddCustomImage from './components/AddCustomImage.vue';
import EditCustomImage from './components/EditCustomImage.vue';
//import CustomTexts from './components/CustomTexts.vue';
//import EditCustomText from './components/EditCustomText.vue';
import Events from './components/Events.vue';
import EditEvent from './components/EditEvent.vue';
import EditLoop from './components/EditLoop.vue';
import EditEventCondition from './components/EditEventCondition.vue';
import SaveOnline from './components/SaveOnline.vue';
import EditEventAction from './components/EditEventAction.vue';
import InsertExpression from './components/InsertExpression.vue';
import ImageSelection from './components/ImageSelection.vue';
import ObjectTypeSelection from './components/ObjectTypeSelection.vue';
//import {setCookie} from '@/lib/mmgUtil.js';
//import {buildViewModel} from '@/lib/GameEditorViewModel.js';
//import {ActionTemplateController} from '@/lib/mmg/snapshot/ActionTemplateController.js';

export default {
  name: 'EditGame',
  components: {
    Spinner, IconHelp, IconPlay, IconAdd, IconRestart, IconEdit, IconFullscreen, IconShowMore, IconShowLess,
    Header, ImportExport, Levels, EditLevel, EditLevelVariable, CustomObjectTypes, EditCustomObject, EditButton,
    CustomObjectTypesTab, CustomObjectTypeBehaviour, EditObjectVariable, GlobalVariables, EditGlobalVariable,
    /*CustomTexts, EditCustomText,*/ Events, EditEvent, EditLoop, EditEventCondition, SaveOnline, EditEventAction,
    InsertExpression, ImageSelection, ObjectTypeSelection, EditLevelText, CustomImages, AddCustomImage, EditCustomImage
  },
  methods: {

    getGame: function (gameId) {

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'games/' + gameId;
      const config = {};

      return axios.get(url, config);

    }
    
  },
  data() {
    return {
      game: null,
      loading: false,
      errorMessage: null,
      showHeader: false,
      info: null,
      sessionId: null,
      saveGameForm: {
        gameId: "",
        gameParamsJson: ""
      },
      showForm: true,
      gameDetails: {
        name: "",
        userId: "",
        createdDate: "",
        updatedDate: ""
      },
      vm: {
        isPlayingGame: false,
        oldLevel: null,
        newLevelVariable: null
      }
    }
  },
  mounted () {

    let that = this;

    that.loading = true;

    let gameId = that.$route.query.gameId;

    if (gameId != null) {

      this
        .getGame(gameId)
        .then(function(response) {
          that.game = response.data.Item;
          let templateVersion = that.game.templateVersion.S;
          return loadEngine(templateVersion);
        })
        .then(function() {
          let newVm = new EasyGameMakerLibrary.ActionTemplateController(process.env.VUE_APP_RESOURCES_BASE_URL); // eslint-disable-line no-undef
          newVm.setGame(that.game);

          that.vm = newVm;
          that.loading = false;
        });

    } else {

      let templateVersion = "snapshot";

      loadEngine(templateVersion)
        .then(function() {
          let newVm = new EasyGameMakerLibrary.ActionTemplateController(process.env.VUE_APP_RESOURCES_BASE_URL); // eslint-disable-line no-undef

          that.vm = newVm;
          that.loading = false;
        });

    }

  },
  destroyed () {
    this.vm.removeEventListeners();
  }
}
</script>


