<template>

	<div v-if="vm.currentScreen=='ADD_CUSTOM_IMAGE'">
		<h2>Upload image</h2>
		<table class="hiscores">
			<tr>
				<td>image</td>
				<td>
					<input type="file" id="inputImage" @change="onImageUpload" />
					<p style="margin-top: 10px">
						<img v-if="vm.newCustomImage.imageData" :src="vm.newCustomImage.imageData" class="image-large"/>
					</p>
				</td>
			</tr>
			<tr>
				<td>name</td><td><input type="text" v-model="vm.newCustomImage.name"></td>
			</tr>
		</table>
		<br>
		<button type="button" class="btn btn-primary" v-on:click="vm.addCustomImage();">upload</button>
		<button type="button" class="btn btn-secondary" v-on:click="vm.cancelAddCustomImage();">cancel</button>
		<br><br>
	</div>
  
</template>

<script>
export default {
  name: 'AddCustomImage',
  props: {
    vm: Object
  },
  methods: {
    onImageUpload(e) {
		const file = e.target.files[0];
		this.vm.newCustomImage.name = file.name;
		//this.vm.newCustomImage.imageData = URL.createObjectURL(file);
		//this.getBase64(file);
		//this.$forceUpdate(); //TODO: verificar como fazer para no precisar do forceUpdate
		let that = this;
		let reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = function () {
			that.vm.newCustomImage.imageData = reader.result;
			that.$forceUpdate(); //TODO: verificar como fazer para no precisar do forceUpdate
		};
		reader.onerror = function (error) {
			console.log('Error: ', error);
		};
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.image-large {
	max-width: 128px;
	max-height: 128px;
}

</style>
