				<a v-show="currentImageSourceTypeId=='UPLOAD'" class="nav-link" href="" v-on:click.prevent="setCurrentImageSourceType('LIBRARY');" id="link-tab-IMAGE_SELECTION_LIBRARY">Image library</a>
