			</span>

			<div class="flex-container">
				<div class="service-box" v-for="image in currentImages" v-bind:key="image.id" v-on:click="vm.selectImage(image.id)">
					<img class="image" :id="image.id" :src="resourcesBaseUrl + image.id">
				</div>
			</div>
		</div>

		<div v-show="currentImageSourceTypeId=='UPLOAD'">
