				</div>
			</div>

			<br>
			<button type="button" v-on:click="vm.goToAddCustomImage()" class="btn btn-primary">upload image</button>
		</div>


		<br>
		<button type="button" v-on:click="vm.closeImageSelection()" class="btn btn-secondary">cancel</button>
		<br>

	</div>

</template>

